import { GetComponentProps, useEditor, useEditorActions } from 'topwrite';
import { ReactComponent as Icon } from '../images/icon.svg';

export default function Tool({ Component }: GetComponentProps<'editor:tool'>) {

    const editor = useEditor();

    const handler = () => {
        const { insertXBlock } = useEditorActions();
        insertXBlock(editor, {
            name: 'sql',
            value: ''
        });
    };

    return <Component
        title={'插入SQL表格'}
        handler={handler}
    >
        <Icon />
    </Component>;
}
